.Service{
    background: whitesmoke;
    
}
.Service-header{
    display: flex;
    padding: 3rem 0;
}

.Service-title{
    justify-content: center;
    text-align: center;
    padding: 10px;
    letter-spacing: 3px;
}
.Service-title h1{
    padding: 10px;
}
.Service-title p{
    font-family: 'Poppins',sans-serif;
    font-size: 15px;
}
.card-wrapper{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 2rem;
    grid-row-gap:2rem ;
    padding: 2rem;
}
.card{
    color: #2f2d2d;
    background: #fff;
    padding: 2rem 2rem;
    margin: 20px;
    padding-top: 4rem;
    position: relative;
    text-align: center;
}
.card:hover{
    color: #fff;
    background: #2f2d2d;
    transition: 0.5s linear;
}
.card i{
    position:absolute;
    top:-2.5rem;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    background: rgb(255,106, 0);
    border-radius: 10px;
    height: 70px;
    width: 70px;
    font-size: 50px;
   
}
.card h2{
    
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 500;
    margin-bottom: 1rem;
}
.card p{
    font-family: 'Lato', sans-serif;
    line-height: 1.5;
}
