
.header{
    font-family: "Poppins Medium";
    background: #2f2d2d;
    clip-path: polygon(0 0,100% 0,100% 100%,70% 90%,0 100%);
}
.container{
    display: flex;
    text-align: center;
    justify-content: center;
    max-width: 1152px;
    padding: 0 15px;
    margin: 0 auto;
}   
.logo h1{
    display: flex;
    padding: 10px;
    color: rgb(242, 221, 221);
    letter-spacing: 3px;
    
} 
span{
    color: rgb(255, 106, 0);
}
span:hover{
    color: #03e9f4;
    transition: 1s;
}
.menu{
    display:flex;
    text-align: center;
    padding: 10px;
    margin: 0 200px 0 200px;
    text-transform: uppercase;
    justify-content: center;
}
.menu li,.menu a{
    text-decoration: none;
    list-style: none;
    padding: 10px;
    margin: 0 0 0 20px;
    color: #ffffff;
    

}
.menu li,.menu a:hover{
    color: rgb(255, 106, 0);
    transition: 1s;
}
.social{
    display: flex;
    transition: 1s;
}
.social a{
    color: aliceblue;
    padding: 10px;
    font-size: 20px;
    margin: 10px;
    align-items: center;
    justify-content: center;
  
}
