.Resume{
    height: 700px;
    background: whitesmoke;
    
}
.resume-header{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}
.title{
    padding: 20px;
    justify-content: center;
    text-align: center;   
    letter-spacing: 3px;   
}
.resume-container{
    position: relative;
    text-align: center;
    
}
.nav-data{
    justify-content: center;
    align-items: center;
    position: relative;
    width: 300px;
    box-shadow: 5px 0.1px  3px #958f8f;
    margin-left: 200px;
}
.navItems{
    position: relative;
    background-color: #2f2d2d;
    display:flex;
    border: 2px solid #2f2d2d;
    box-sizing: initial;
    width:50px;
    text-align: center;
    align-items: center;
    transition: 0.5s;
}
.navItems ul li{
    position: relative;
    list-style: none;
    width: 100%;
    margin: 20px 50px 0px 0px;
}
.navItems ul li a:hover{
    color: #fff;
    background: #2f2d2d;
    border-radius: 20px;
    
  transition: 0.8s ease;
}
.navItems ul li a{
    position: relative; 
    text-decoration: none;
    text-align: none;
    color: #111;
    display: flex;    
    gap: 50px;
    font-weight: 500;
    align-items: center;
    justify-content:flex-start;
    overflow-x: hidden;
}
.navItems li a i{
    position: relative;
    font: 18px;
    padding: 10px;
    color: #fff;
}

.Details{
    list-style: none;
}
.Details .study-info img{
    height: 15px;
    width: 15px;
    object-fit: cover;
}
.study-info {
    position: relative;
    margin-top: -340px;
    margin-left: 500px;
    text-align: start;
    padding: 10px 30px;
}
.study-info .college{
    margin: 20px;
    color: rgb(255, 106, 0);
    font-weight: 600;
    word-spacing: 10px;
    font-size: 20px;
}
.study-info .course{
    font-weight: 500;
    word-spacing: 10px;
    margin-left: 35px;

}
.study-info .percentage{
    font-size: 13px;
    margin-bottom: 30px;
    word-spacing: 10px;
    margin-left: 35px;
    visibility: visible;
}
.study-info .date{
    position: relative;
    background: rgb(255, 106, 0);
    color: #fff;
    padding: 10px 10px;
    margin: 10px auto;
    border-radius: 20px;
}
.work-info{
    position: relative;
    text-align: start;
    padding: 10px  30px;
    margin-top: -340px;
    margin-left: 500px;
}
.work-info img{
    height: 15px;
    width: 15px;
    object-fit: cover;
}
.work-info .com-name{
    padding: 20px;
    color: rgb(255, 106, 0);
    font-weight: 600;
    word-spacing: 10px;
    font-size: 20px;
}
.work-info .date{
    text-align: center;
    background: rgb(255, 106, 0);
    color: #fff;
    padding: 10px 10px;
    margin-left: 25rem;
    border-radius: 20px;
}
.work-info .com-role{
    margin-left: 40px;
    margin-bottom: 20px;
    margin-top: 20px;
    font-weight: 600;
}
.work-info p{
    margin-left: 40px;
    position: relative;
    width: 710px;
}
.skill-info{
    position: relative;
    padding: 10px 30px;
    display: grid;
    grid-template-columns: 0fr 0fr;
    column-gap: 5rem;
    margin-top: -340px;
    margin-left: 500px;
    text-align: start;
}
.skill-info img{
    height: 15px;
    width:15px;
    object-fit: cover;
}
.skill-info .skill-name{
    margin-left: 20px;
    font-weight: 600;
    font-size: 20px;
    color: rgb(255, 106, 0);

}
.range{
    margin-bottom: 40px;
    margin-left: 35px;
    width: 200px;
    height: 15px;
    -webkit-appearance: none;
    background: rgb(255, 106, 0);
    overflow: hidden;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 1);

}
.range::-webkit-slider-thumb{
    -webkit-appearance: none;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #2f2d2d;
    cursor: pointer;
    border: 4px #111;
    box-shadow: -400px 0 0 400px #2f2d2d;
}
.Project-info{
    position: relative;
    padding: 10px 30px;
    column-gap: 5rem;
    margin-top: -340px;
    margin-left: 500px;
    text-align: start;
}
.Project-info img{
    height: 15px;
    width:15px;
    object-fit: cover;
}
.Project-info .Project{
    margin-left: 20px;
    font-weight: 600;
    font-size: 20px;
    color: rgb(255, 106, 0);
}
.Project-info .course{
    font-weight: 500;
    margin-left: 40px;
    margin-top: 20px;
}
.Project-info p{
    margin-top:-20px;
    margin-left: 40px;
}
.Interest-info{
    position: relative;
    padding: 10px 30px;
    column-gap: 5rem;
    margin-top: -340px;
    margin-left: 500px;
    text-align: start;
}
.Interest-info .Interest{
    margin-left: 20px;
    font-weight: 600;
    font-size: 20px;
    color: rgb(255, 106, 0);
}
.Interest-info p{
    margin-left: 40px;
    margin-bottom: 30px;
}
.Interest-info img{
    height: 15px;
    width:15px;
    object-fit: cover;
}
