.home{
    display: flex;
    padding: 3rem 0;
}
.home img{
    height: 500px;
    margin: 0 100px;
    border-radius: 180px;
}
.right{
    color: #fff;
    height: 400px;
    margin: 50px;
    justify-content: center;
    width: 600px;
}
.right h6{
    font-size: 20px;
    padding: 15px;
}
.right p{
    padding: 15px;
}
.right h1{
    font-size: 70px;
    padding: 15px;
}
.download-btn .cv-btn{
    position: relative;
    display: inline-block;
    padding: 15px;
    margin: 15px;
    color: rgb(255, 106, 0);
    border-color: rgb(255,106, 0);
    background: transparent;
    text-decoration: none;
    text-transform: uppercase;
    transition: 1s;
    letter-spacing: 4px;
    overflow: hidden;
 }
 .download-btn .cv-btn:hover{
    color: #03e9f4;
    border-color: #03e9f4;
    
 }

