@import url('https://fonts.googleapis.com/css?family=Raleway:wght@400;700&display=swap');

.About{
    justify-content: center;
    padding:  0;
    
}
.about-me{
    justify-content: center;
}
.about-me h1{
    font-size: 40px;
    justify-content: center;
    text-align: center;
    padding: 10px;
    letter-spacing: 3px;
}
.about-me h5{   
    justify-content: center;
    text-align: center;
    letter-spacing: 3px;
}
.image{
    display: flex;
    margin: 50px auto;
    
    justify-content: center;
    box-shadow: 0 0 10px #111;
}
.image img{
    object-fit:cover;
    object-position:0 0px;
    height: 580px;
    width: 600px;
}
.description{
    padding: 10px;
    background: whitesmoke;
}
.description h3{
    margin: 10px;
}
.description p{
    padding: 10px;
     margin: 10px 0 80px;
}
.description h2{
    margin: 10px;

}
.highlight li{
    list-style: none;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    font-weight: lighter;
    padding: 10px 0px;
    
    
}
.highlight img{
    height: 15px;
    width:15px;
    object-fit: cover;
    margin-right:15px;
}