.Contact{
    background: whitesmoke;
}
.Container{
    border-radius: 20px;
    box-shadow: 0 0 15px #112;
    position: relative;
    padding: 0 20px;
    background-color: #112;
    display: flex;
    align-items: center;
}
.Container .heading{
    color: #fff;
    letter-spacing: 3px;
    position: relative;
    margin-bottom: 400px;
}
.Container .heading img{
    height: 50px;
    position: relative;
}
.Container .img{
    bottom: 20px;
    width: 60%;
    margin: 0 10px;
    position: absolute;
    height: 60%;
}
.Container h4{
    color: #fff;
    position: absolute;
    padding-top: 40px;
    padding-left: 20px;
    letter-spacing: 3px;
    margin-bottom: 200px;
}
.Container-info{
    padding: 20px 100px;
}
.Container .social-info{
    position: absolute;
    margin-bottom: 300px;
    margin-right: 400px;
}
.Container .social-info i{
    padding: 0 20px;
    color: #fff;
    position: relative;
    font-size: 15px;

}
.Contact-info{
    width: 400px;
    padding: 20px;
    border-radius: 20px;
    position:relative;
    
    margin-left: 600px;
    background: #fff;
    list-style: none;
}
.Contact-info h6{
    letter-spacing: 3px;
    font-size: 15px;
}
.Contact-info input{
    margin-bottom: 20px;
    border-radius: 5px;
    border: rgb(231, 228, 228);
    width: 100%;
    height: 35px;
    background: rgb(231, 228, 228);
}
.Contact-info textarea{
    background: rgb(231, 228, 228);
    border-radius: 5px;
    width: 100%;
    border: rgb(231, 228, 228);
    margin-bottom: 20px;
}
.Contact-info .btn{
    padding: 5px 50px;
    font-size: 20px;
    border-radius: 20px;
    border:2px solid rgb(255, 106, 0);
    background: #112;
    color: #fff;
    letter-spacing: 3px;
}
.Contact-info  img{
    color: #fff;
    height: 25px;
}